@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom Gilroy Font (fallback to Inter) */
@font-face {
  font-family: 'Gilroy';
  src: url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');
  font-weight: normal;
  font-style: normal;
}

/* Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Gilroy', 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
}

/* Custom Components */
@layer components {
  .btn-primary {
    @apply bg-secondary-500 hover:bg-secondary-600 text-white font-semibold py-3 px-6 rounded-full transition-all duration-300 transform hover:-translate-y-0.5 hover:shadow-lg;
  }
  
  .btn-secondary {
    @apply border-2 border-primary-500 text-primary-500 hover:bg-primary-500 hover:text-white font-semibold py-3 px-6 rounded-full transition-all duration-300;
  }
  
  .input-field {
    @apply w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-secondary-500 focus:border-transparent transition-all duration-300;
  }
  
  .card {
    @apply bg-white rounded-2xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1;
  }
  
  .section-padding {
    @apply py-16 px-4 sm:px-6 lg:px-8;
  }
  
  .container-max {
    @apply max-w-7xl mx-auto;
  }
  
  .text-gradient {
    @apply bg-gradient-to-r from-primary-500 to-secondary-500 bg-clip-text text-transparent;
  }
}

/* Custom Utilities */
@layer utilities {
  .animate-float {
    animation: float 6s ease-in-out infinite;
  }
  
  .animate-pulse-soft {
    animation: pulse-soft 3s ease-in-out infinite;
  }
  
  .glass-effect {
    @apply bg-white/80 backdrop-blur-lg border border-white/20;
  }
  
  .gradient-bg {
    background: linear-gradient(135deg, #0B1956 0%, #FF5288 100%);
  }
  
  .gradient-bg-light {
    background: linear-gradient(135deg, #FFFDEF 0%, #f8fafc 100%);
  }
}

/* Custom Animations */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes pulse-soft {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #FF5288;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #e6004d;
}

/* Form Styling */
.form-group {
  @apply mb-6;
}

.form-label {
  @apply block text-sm font-semibold mb-2 text-primary-500;
}

.form-error {
  @apply text-red-500 text-sm mt-1;
}

.form-success {
  @apply text-green-500 text-sm mt-1;
}

/* Loading States */
.loading-spinner {
  @apply inline-block w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin;
}

.skeleton {
  @apply bg-gray-200 rounded animate-pulse;
}

/* Responsive Typography */
.text-hero {
  @apply text-4xl md:text-6xl font-bold leading-tight;
}

.text-section {
  @apply text-3xl md:text-4xl font-bold mb-6;
}

.text-subsection {
  @apply text-xl md:text-2xl font-semibold mb-4;
}

/* Mobile-first responsive design helpers */
@media (max-width: 640px) {
  .mobile-padding {
    @apply px-4;
  }
  
  .mobile-text {
    @apply text-sm;
  }
}

/* Print styles */
@media print {
  .no-print {
    display: none !important;
  }
}